import { _decorator, Component, Node, Vec3 } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Player')
export class Player extends Component {
  @property(Number) movementSpeed: number = 0.01;

  public movePlayer(offset: Vec3) {
    const scaledOffset = offset.normalize().multiplyScalar(this.movementSpeed * 0.9);
    const newPosition = this.node.getPosition().add(scaledOffset);
    this.node.setPosition(newPosition);
  }
}


